import uniDeCode from "../../components/wakary-input/wakary-input.vue"
import digitalRolling from "@/components/digital-rolling/digital-rolling.vue"
export default {
	components: {
		digitalRolling,
		uniDeCode
	},
	data() {
		return {
			ivalue: Number,
			inputval: Number,
			panduan: 1,
			qcode: 0,
			phone1: 0,
			vlogshow: false,
			openshow: false,
			digitTo: '0',
			digitFrom: '0',
			start: 0
		}
	},
	onReady: function() {
		// 页面跳转后初始化状态
		this.panduan += 1
	},
	onLoad: function(option) {
		this.phone = option.phone
		this.qcode = option.id
		var reg = /(\d{3})\d{4}(\d{4})/;
		this.phone1 = this.phone.replace(reg, '$1****$2')

	},
	computed: {
		price() {
			let pr = Math.random() * 100
			if (pr < 70) {
				 let a = parseInt(Math.random() * 5+1)
				return	a
			} else {
				let b = parseInt(Math.random() * 5 + 5)
				return  b
			}
		}
	},
	watch: {
		inputval: {
			immediate: true,
			handler: function(newVal) {
				// 进入页面初始化
				// 验证短信码是否争取，正确跳转，不正确显示
				var that = this
				if (Number(this.qcode) == Number(this.inputval)) {
					// 成功后,隐藏手机键盘
					uni.hideKeyboard()
					uni.showLoading({
						title: '验证中',
						success: function() {
							uni.setStorageSync("phone", that.phone)
							setTimeout(function() {
								uni.hideLoading()
								// 成功后跳转
								uni.switchTab({
									url: '/pages/me/me?nowphone=' + that.phone
								})
							}, 2000);
						}
					});
				} else if (Number(this.qcode) != Number(this.inputval) && this.panduan != 1) {
					uni.showToast({
						title: '验证码错误',
						duration: 3000,
						icon:'none',
						success:res=>{
							setTimeout(()=>{
								uni.redirectTo({
									url:"/pages/sigUp/sigUp"
								})
							},2000)
						}
					})
				}
			}
		}
	},
	methods: {

		close(type) {
			switch (type) {
				case 0:
					console.log(0)
					this.vlogshow = false
					break;
				case 1:
					console.log(1)
					this.openshow = false
					break;
				default:
					break;
			}
		},
		open() {
			this.vlogshow = false
			this.openshow = true
		},
		change() {
			this.vlogshow = true
		},
		inputValue: function(e) {
			this.inputval = e
		}
	}
}
